	setHightLightsCells(move){
		//alert(move);
		//alert(move.substring(-2,1).toLowerCase());
		var x = 0;
		if(move.substring(-2,1).toLowerCase() === "a")x = 45;
		if(move.substring(-2,1).toLowerCase() === "b")x = 90;
		if(move.substring(-2,1).toLowerCase() === "c")x = 135;
		if(move.substring(-2,1).toLowerCase() === "d")x = 180;
		if(move.substring(-2,1).toLowerCase() === "e")x = 225;
		if(move.substring(-2,1).toLowerCase() === "f")x = 270;
		if(move.substring(-2,1).toLowerCase() === "g")x = 315;
		if(move.substring(-2,1).toLowerCase() === "h")x = 360;
		
		var y = 0;
		switch (parseInt(move.slice(-1))) {
			case 1:
				y = 315;
				break;
			case 2:
				y = 270;
				break;
			case 3:
				y = 225;
				break;
			case 4:
				y = 180;
				break;
			case 5:
				y = 135;
				break;
			case 6:
				y = 90;
				break;
			case 7:
				y = 45;
				break;
			case 8:
				y = 0;
				break;
		}
		//alert(x+"-"+y);
		const a = [];
		a.push(x,y);
		return a;
	}

	isEmpty(val){
		return (val === undefined || val == null || val.length <= 0) ? true : false;
	}

	HistoryText(history){
		var history_text ="";
		if(history != null){
		if(history.length>0) {
		  for (var i = 0; i < history.length; i++) {
			var newline = (i>0)?((i%4 == 0)?"\n":"       "):"\n";
			history_text = history_text + newline + JSON.stringify(history[i]).split(",")[1].split(":")[1].replaceAll("} ","&emsp;").replaceAll("}","").replaceAll("\"","");
		  }
		}
		}
		return history_text.replaceAll("(","").replaceAll(")","").replaceAll(",",":");
	  }

